<template>
    <div class="view-style" :style="`background-image:url(${require('@/assets/image/main_back/home_image_2_a.png')})`">
        <div class="main-style" @click="routerEvent('MainGame')"><i class="icon-back1" style="vertical-align:bottom"/></div>

        <!-- 卡牌展示 -->
        <div v-if="deckShow" class="deck-style" id="deck-id" @mouseleave="deckLeaveEvent">
            <div class="deck-model-style" :style="style" id="deck-model-id" ref="deck-model" @mousemove="deckModelMoveEvent" @mouseover="deckModelOverEvent">
                <div class="model-after-style" ref="model-after"></div>
            </div>
        </div>

        <!-- 卡牌组 -->
        <div class="deck-list-style" id="deck-list-id">
            <div class="list-item-style" v-for="item in dataList" :key="item.id" :id="`list-item-${item.id}`" :style="`background-image:url(${item.backImageTop}),url(${item.backImageBottom})`" @click="deckClickEvent(item)"></div>
        </div>

        <!-- 卡牌信息 -->
        <!-- <div class="deck-history-style" id="deck-history-id" :style="`border-image:url(${require('@/assets/image/border/border_1.png')}) 100 100 round;`"> -->
        <div class="deck-history-style" id="deck-history-id">
            <div v-if="deckShow" class="deck-history-item1">{{deckDetails.chineseName}} «<span class="lv-style" style="color:#c4d1df">{{deckDetails.deckLv}}</span>»<br>{{deckDetails.foreignName}}</div>
            <div v-if="deckShow" class="deck-history-item2" :style="`background-image:url(${require('@/assets/image/div_line/div-line-1.png')})`"></div>
            <div v-if="deckShow" class="deck-history-item3">
                <div class="switch-info-prev">《</div>
                <div class="switch-info"></div>
                <div class="switch-info-next">》</div>
            </div>
        </div>

        <!-- 换背景 -->
        <div class="switch-back-style">
            <span class="switch-back-prev" :style="`visibility:${switchBackList.id == 0 ? 'hidden' : 'visible'}`" @click="switchBack('prev')">《</span>
            <div class="switch-back-item" :id="`switch-back-item-${item.id}`" v-for="item in switchBackList.data[switchBackList.id]" :key="item.id" :style="`background-image:url(${item.imgUrl})`" @click="deckChangeEvent(item)"></div>
            <span class="switch-back-next" :style="`visibility:${switchBackList.data == 0 || switchBackList.data.length - 1 == switchBackList.id ? 'hidden' : 'visible'}`" @click="switchBack('next')">》</span>
        </div>
    </div>
</template>

<script>
// import * as TweenMax from 'gsap/all'
import {cardEnum,backEnum} from "@/api/enum.js"
import _ from 'lodash'
export default {
    name:"MainDeck",
    data(){
        return{
            dataList:[],
            deckDetails:{},
            deckShow:false,
            switchBackList:{
                id:0,
                data:[]
            },
            style:{}
        }
    },
    mounted(){
        //366,691 r,sr,ssr,ur
        // console.log(document.getElementById("deck-model-id").offsetWidth,document.getElementById("deck-model-id").offsetHeight);
        //124,199
        // console.log(document.getElementById("list-item-1").offsetWidth,document.getElementById("list-item-1").offsetHeight);
        this.getDeckList();
        this.pictureAddressing()
    },
    methods:{
        routerEvent(e){
            this.$router.push({name:e})
        },
        deckModelMoveEvent(e){
            const dom1 = this.$refs['deck-model'];
            const dom2 = this.$refs['model-after'];
            const calcValue = function calcValue(a, b) {return Number((a / b * 40 - 40 / 2).toFixed(1));};
            const yValue = calcValue(e.offsetY, dom1.offsetHeight);
            const xValue = calcValue(e.offsetX, dom1.offsetWidth);

            const relX = (e.offsetX + 1) / dom1.offsetWidth;
            const relY = (e.offsetY + 1) / dom1.offsetHeight;
            const rotX = `rotateX(${(relY - 0.5) * -30}deg)`;
            const rotY = `rotateY(${(relX - 0.5) * 30}deg)`;
            dom1.style.transform = `perspective(1500px) ${rotY} ${rotX}`;
            dom1.style.backgroundPosition = `${this.deckDetails.backImageDecorate == null ? '' : '0% 100%,'}calc(50% + ${-xValue}px) ${yValue}px,50% 50%`;

            const lightX = this.scale(relX, 0, 1, 150, -50);
            const lightY = this.scale(relY, 0, 1, 30, -100);
            const lightConstrain = Math.min(Math.max(relY, 0.3), 0.7);
            const lightOpacity = this.scale(lightConstrain, 0.3, 1, 1, 0) * 255;
            const lightShade = `rgba(${lightOpacity}, ${lightOpacity}, ${lightOpacity}, 1)`;
            const lightShadeBlack = `rgba(255, 255, 255, 0)`;
            dom2.style.backgroundImage = `radial-gradient(circle at ${this.deckDetails.backImageDecorate == null ? '' : '0% 100%, '}${lightX}% ${lightY}%, ${lightShade} 20%, ${lightShadeBlack})`;
        },
        deckLeaveEvent(e){
            const dom1 = this.$refs['deck-model'];
            const dom2 = this.$refs['model-after'];
            dom1.style.transform = ``;
            dom1.style.backgroundPosition = `${this.deckDetails.backImageDecorate == null ? '' : '0% 100%,'}50% 0px,50% 50%`;
            dom2.style.opacity = 0;
        },
        deckModelOverEvent(){
            const dom = this.$refs['model-after'];
            dom.style.opacity = 1;
        },
        //获取人物卡牌
        getDeckList(){
            var obj_1 = this.$store.state.archive.archive
            // console.log(obj_1);
            if(obj_1.own_cards.length == 0){
                return '';
            }else{
                cardEnum().then((res)=>{
                    let list = res;
                    let obj_2 = {};
                    if(obj_1.own_cards.length == res.length){
                        this.dataList = _.cloneDeep(list);
                    }else if(obj_1.own_cards.length < res.length){
                        obj_1.own_cards.forEach((item,index)=>{
                            obj_2 = _.cloneDeep(list[_.findIndex(list,(e)=>{return e.id == item})]);
                            this.dataList.push(obj_2)
                        })
                    }
                })
            }
        },
        //人物牌点击事件
        deckClickEvent(e){
            // console.log(e);
            this.deckShow = true;
            if(this.deckDetails.id && this.deckDetails.id == e.id){
                return false;
            }
            this.deckDetails = {};
            this.deckDetails = _.cloneDeep(e);
            this.style = {
                backgroundImage:`${this.deckDetails.backImageDecorate == null ? '' : 'url(' + this.deckDetails.backImageDecorate + '),'}url(${this.deckDetails.backImageTop}),url(${this.deckDetails.backImageBottom})`,
                backgroundSize:`${this.deckDetails.backImageDecorate == null ? '' : 'contain,'}cover,cover`,
                backgroundPosition:`${this.deckDetails.backImageDecorate == null ? '' : '0% 100%,'}50% 0px,50% 50%`
            }
            this.palyAudio(e.audio);
        },
        palyAudio(e){
            if(e == 'null'){
                if(this.audio){
                    this.audio.pause();
                }
                return false;
            }
            var mp3 = require('@/assets/audio/' + e + '.mp3')
            this.audio = new Audio();
            this.audio.src = mp3;
            this.audio.play();
        },
        //获取背景
        pictureAddressing(){
            var obj_1 = this.$store.state.archive.archive
            if(obj_1.unlock_background.length == 0){
                return '';
            }else{
                backEnum().then((res)=>{
                    let list = res;
                    let list1 = [];
                    let obj_2 = {};
                    if(obj_1.unlock_background.length == res.length){
                        this.switchBackList.data = _.chunk(list,3);
                    }else if(obj_1.unlock_background.length < res.length){
                        obj_1.unlock_background.forEach((item,index)=>{
                            obj_2 = _.cloneDeep(list[_.findIndex(list,(e)=>{return e.id == item})]);
                            list1.push(obj_2)
                        })
                        this.switchBackList.data = _.chunk(list1,3)
                    }
                })
            }
        },
        switchBack(e){
            switch (e) {
                case 'prev':
                    this.switchBackList.id--
                    break;
                case 'next':
                    this.switchBackList.id++
                    break;
                default:
                    break;
            }
        },
        deckChangeEvent(e){
            if(this.deckShow == false){
                return false;
            }
            this.$refs['deck-model'].style.backgroundImage = `${this.deckDetails.backImageDecorate == null ? '' : 'url(' + this.deckDetails.backImageDecorate + '),'}url(${this.deckDetails.backImageTop}),url(${e.imgUrl})`;
        },
        scale: (val, inMin, inMax, outMin, outMax) => outMin + (val - inMin) * (outMax - outMin) / (inMax - inMin)
    }
}
</script>

<style lang="scss" scoped>
.view-style{
    width: 100%;
    height: 100%;
}
.main-style{
    position: absolute;
    z-index: 9;
    left: 2%;
    top: 10px;
    color: rgba(127, 255, 212, 0.527);
    font-size: 20px;
    font-family: "Goudy Old Style", Courier, monospace;
    transition: 0.2s all;
    cursor: pointer;
    text-shadow:2px 2px #0000007c;
}
.main-style:hover{
    letter-spacing: 3px;
    color: rgba(127, 255, 212, 1);
}
.deck-style{
    position: absolute;
    z-index: 2;
    width: 34%;
    height: 80%;
    top: 10%;
    left: 33%;
    // border: 2px solid black;
}
.deck-model-style{
    position: absolute;
    z-index: 2;
    width: 56%;
    height: 80%;
    top: 8%;
    left: 22%;
    border-radius: 10px;
    // background-color: aquamarine;
    transition: 0.1s all ease;
    cursor: pointer;
    // background-position: 0% 100%,50% 0px,50% 50%;
    // background-size: contain,cover,cover;
    background-repeat: no-repeat;
    // border: 2px solid black;
}
.model-after-style{
    position: absolute;
    top: 0%;
    left: 0%;
    z-index: 2;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    mix-blend-mode: soft-light;
    transition: 0.1s all ease;
}
.deck-list-style{
    position: absolute;
    z-index: 2;
    width: 28%;
    height: 80%;
    top: 10%;
    left: 3%;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    justify-content: space-evenly;
    overflow-x: hidden;
    overflow-y: scroll;
}
.deck-list-style::-webkit-scrollbar{
    width: 0;
}
.deck-history-style{
    position: absolute;
    z-index: 2;
    width: 28%;
    height: 80%;
    top: 10%;
    right: 3%;
    border: 15px solid transparent;
    font-family: "幼圆", Courier, monospace;
    font-size: 35px;
}
.deck-history-item1{
    font-family: "幼圆", Courier, monospace;
    font-size: 35px;
    margin-top: 15px;
}
.deck-history-item2{
    width: 100%;
    height: 20px;
    // background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}
.deck-history-item3{
    width: 100%;
    height: 80%;
    margin-top: 30px;
    // border: 1px solid black;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-evenly;
    align-items: center;
}
.list-item-style{
    width: 23%;
    height: 23%;
    border-radius: 10px;
    margin-top: 5px;
    // border: 2px solid red;
    background-color: rgba(240, 255, 240, 0.2);
    cursor: pointer;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}
.list-item-style:hover{
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.30);
}
.lv-style{
    font-family: "华文隶书", Courier, monospace;
}
.switch-back-style{
    position: absolute;
    z-index: 2;
    bottom: 20px;
    left: calc(50% - 170px);
    width: 340px;
    height: 158px;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-evenly;
    align-items: center;
    border-top: 1px solid rgba(240, 255, 240, 0.5);
    border-bottom: 1px solid rgba(240, 255, 240, 0.5);
    // border: 2px solid black;
}
.switch-back-item{
    width: 72px;
    height: 138px;
    border-radius: 10px;
    // border: 1px solid black;
    cursor: pointer;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    text-align: center;
}
.switch-back-next{
    color: rgba(240, 255, 240, 1);
    font-size: 20px;
    // line-height: 158px;
    cursor: pointer;
}
.switch-back-prev{
    color: rgba(240, 255, 240, 1);
    font-size: 20px;
    // line-height: 158px;
    cursor: pointer;
}
.switch-info{
    width: 85%;
    height: 100%;
    border: 1px solid black;
}
.switch-info-next{
    color: rgba(240, 255, 240, 1);
    font-size: 20px;
    // line-height: 158px;
    cursor: pointer;
    width: 5%;
}
.switch-info-prev{
    color: rgba(240, 255, 240, 1);
    font-size: 20px;
    // line-height: 158px;
    cursor: pointer;
    width: 5%;
}
</style>